<template>
  <div class="Blog" v-if="blogList.length > 0">
    <div class="banner">
      <div class="placeholder"></div>
      <div class="details">
        <div class="title wbba">
          {{ $t("index.switch") === "en" ? blogList[0].blogTitleEn : blogList[0].blogTitle }}
        </div>
        <div class="date">{{ blogList[0].createTime }} {{ blogList[0].createBy }}</div>
        <div
          class="text"
          v-html="$t('index.switch') === 'en' ? blogList[0].textEn : blogList[0].text"
        ></div>
        <div class="btns">
          <router-link
            class="btn"
            :to="{ path: '/pc/Blog/Details', query: { id: blogList[0].blogId } }"
          >
            More
          </router-link>
          <!-- <div
            class="btn"
            v-if="blogList[0].isSub === 'true'"
            @click="unSubBlog(blogList[0].blogId)"
          >
            取消订阅
          </div> -->
          <div class="btn" @click="dialogVisible = true">{{ $t("index.switch") === "cn" ? '订阅' : 'subscribe' }}</div>
        </div>
      </div>
    </div>
    <div class="content">
      <div class="w1352 w">
        <div class="box-card" v-for="blog in blogList" :key="blog.blogId">
          <router-link :to="{ path: '/pc/Blog/Details', query: { id: blog.blogId } }">
            <div class="images">
              <img :src="blog.images" alt="" />
            </div>
            <div class="main">
              <div class="top">
                {{ $t("index.switch") === "en" ? blog.blogTitleEn : blog.blogTitle }}
              </div>
              <div
                class="medile"
                v-html="$t('index.switch') === 'en' ? blog.textEn : blog.text"
              ></div>
              <div class="bottom">
                <div class="username">{{ blog.createBy }}</div>
                <div class="date">{{ blog.createTime }}</div>
              </div>
              <!-- <div class="btn">
                <router-link :to="{path: '/pc/Blog/Details', query: {id: blog.blogId}}">
                  <el-button type="text">More</el-button>
                </router-link>
              </div> -->
            </div>
          </router-link>
        </div>
        <div class="box-card"></div>
        <div class="box-card"></div>
      </div>
      <div class="more">
        <span v-if="blogList.length<total" @click="more">
          {{ $t("index.switch") === "cn" ? '浏览更多' : 'more' }}
        </span>
      </div>
    </div>
    <el-dialog
      :visible.sync="dialogVisible"
      width="600px"
      :before-close="handleClose"
      :show-close="false"
    >
      <el-input v-model="email" :placeholder="$t('dialog.entere')"></el-input>
      <div class="btn_style">
        <el-button type="primary" @click="subBlog">{{ $t("index.switch") === "cn" ? '订阅' : 'subscribe' }}</el-button>
        <el-button type="text" @click="handleClose">{{ $t("dialog.cancel") }}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { GetBlogList, UnSubBlog, SubBlog } from "@/assets/server/api.js";
import { baseURL, trimHtml } from "@/utils/utils";
export default {
  data() {
    return {
      blogList: [],
      dialogVisible: false,
      email: "",
      pageSize:1,
      total:0
    };
  },
  created() {
    let userName = localStorage.getItem("username");
    document.cookie = "autoLogin=" + userName;
    this.getBlogList();
  },
  methods: {
    handleClose() {
      this.dialogVisible = false;
      this.email = ''
    },
    async subBlog() {
      let id = this.blogList[0].blogId;
      let reg = /\w+@[a-z0-9]+\.[a-z]{2,4}/;
      if (!reg.test(this.email)) {
        return this.$message.warning(this.$t("index.switch") === "cn" ?"请输入正确邮箱":'enter the correct email address');
      }
      let info = id + "/" + this.email;
      const data = await SubBlog(info);
      if (data.code === 0) {
        this.getBlogList();
        this.$message.success(this.$t("index.switch") === "cn" ?"订阅成功!":'success');
        this.handleClose()
      } else {
        this.$message.error(data.msg);
      }
    },
    async unSubBlog(id) {
      const data = await UnSubBlog(id);
      if (data.code === 0) {
        this.getBlogList();
        this.$message.success(this.$t("index.switch") === "cn" ?"取消订阅成功!":'Unsubscribe successfully!');
      } else {
        this.$message.error(data.msg);
      }
    },
    more(){
      this.pageSize++
      this.getBlogList();
    },
    async getBlogList() {
      const data = await GetBlogList({
        pageNo: this.pageSize,
        pageSize: 24
      });
      if (data.code === 0) {
        data.rows.forEach(item => {
          item.images =item.images;
          item.text = trimHtml(item.text);
          let dates = item.createTime.split(" ");
          let times = dates[1].split(":");
          item.createTime = dates[0] + " " + times[0] + ":" + times[1];
        });
        this.total =data.total
        this.blogList = this.blogList.concat(data.rows);
      } else {
        this.$message.error(data.msg);
      }
    }
  }
};
</script>

